import "core-js/modules/es6.array.iterator";
import "core-js/modules/es6.promise";
import "core-js/modules/es7.promise.finally";
import axios from '@/libs/api.request';
var role = new Object();

role.addRole = function (data, fnOk, fnError) {
  axios.request({
    url: '/role/add',
    method: 'post',
    data: data
  }).then(function (res) {
    console.log(res);

    if (res.data.code != "Success") {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};

role.deleteRole = function (data, fnOk, fnError) {
  axios.request({
    url: "/role/delete?id=".concat(data.id),
    method: 'post'
  }).then(function (res) {
    console.log(res);

    if (res.data.code != "Success") {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};

